import { createClient } from '@supabase/supabase-js'

// í™˜ê²½ ë³€ìˆ˜ì—ì„œ Supabase ì„¤ì • ê°€ì ¸ì˜¤ê¸°
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL || 'https://example.supabase.co'
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || 'example-key'

// ê°œë°œ í™˜ê²½ì—ì„œëŠ” ê¸°ë³¸ê°’ ì‚¬ìš©, í”„ë¡œë•ì…˜ì—ì„œëŠ” í™˜ê²½ë³€ìˆ˜ í•„ìˆ˜
if (process.env.NODE_ENV === 'production' && (!process.env.NEXT_PUBLIC_SUPABASE_URL || !process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY)) {
  throw new Error('Missing Supabase environment variables. Please check your .env.local file.')
}

// ë¸Œë¼ìš°ì €ìš© í´ë¼ì´ì–¸íŠ¸ (SSR ì§€ì›)
export const createBrowserSupabaseClient = () => {
  return createClient(supabaseUrl, supabaseAnonKey, {
    auth: {
      autoRefreshToken: true,
      persistSession: true,
      detectSessionInUrl: true
    }
  })
}

// ì„œë²„ìš© í´ë¼ì´ì–¸íŠ¸ (SSR ì§€ì›)
export const createServerSupabaseClient = () => {
  return createClient(supabaseUrl, supabaseAnonKey, {
    auth: {
      autoRefreshToken: false,
      persistSession: false
    }
  })
}

// ê¸°ì¡´ í´ë¼ì´ì–¸íŠ¸ (í•˜ìœ„ í˜¸í™˜ì„±)
export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true
  }
})

